import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import PropTypes from 'prop-types';


const SelectDate = ({ getStat }) => {
    useEffect(() => {getPeriod(localStorage.time)}, [])

    function getPeriod(period) {
        let from = new Date(), to = new Date()
        localStorage.time = period
        switch (period) {
            case 'today': {
                from.setMinutes(0)
                from.setHours(0)
            }; break;
            case 'yestoday': {
                to.setMinutes(0)
                to.setHours(0)
                from = new Date(to - 86400000)
            }; break;
            case 'week': {
                var day = to.getDay(),
                    diff = to.getDate() - day + (day == 0 ? -6 : 1);
                from = new Date(from.setDate(diff));
            }; break;
            case 'month': {
                from.setMinutes(0)
                from.setHours(0)
                from.setDate(1)
            }; break;
            case 'year': {
                from.setMinutes(0)
                from.setHours(0)
                from.setDate(1)
                from.setMonth(0)
            }; break;
            case 'all_time': { 
                from = 0
                to = new Date() 
            }; break;
            default:
                break;
        }
        console.log(Math.floor(from / 1000), from, to, Math.floor(to / 1000))
        getStat(Math.floor(from / 1000), Math.floor(to / 1000))
    }

    return (
        <select defaultValue = {localStorage.time || 'all_time'} onChange={(e) => getPeriod(e.target.value)}>
            <option value = 'today'>Сегодня</option>
            <option value = 'yestoday'>Вчера</option>
            <option value = 'week'>Неделю</option>
            <option value = 'month'>Месяц</option>
            <option value = 'year'>Год</option>
            <option value = 'all_time'>Все время</option>
        </select>
    )
}

export default SelectDate;