import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import '@vkontakte/vkui/dist/vkui.css';
import Stat from './panels/Stat'


const App = () => {
	const [user, setUser] = useState(null)
	const [group, setGroup] = useState(null)
	const [dateFilter, setDateFilter] = useState({begin: 0, end: 0})
	const [token, setToken] = useState(null)
	// var keys = ['subscribed', 'unsubscribed', 'likes', 'comments']

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme')
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light'
				document.body.attributes.setNamedItem(schemeAttribute)
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo')
			setUser(user)
			let token = (await bridge.send("VKWebAppGetAuthToken", { "app_id": 7715875, "scope": "friends,status,groups,stats" })).access_token
			setToken(token)
			let group = await bridge.send("VKWebAppCallAPIMethod", { "method": "groups.get", 
				"params": { "extended": 1, "access_token": token, "v": "5.86" } })
			if('response' in group)
				group = group.response.items.filter(g => g.is_admin == 1)
			setGroup(group)
		}
		fetchData() 

	}, []);

	return (
		group != null ? <Stat token = {token} group = {group} />
		: <p>Загрузка</p>
	);
}

export default App;

