import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import PropTypes from 'prop-types';
import List from './List'
import SelectDay from './SelectDate'
import { withInsets } from '@vkontakte/vkui';


var keys = ['subscribed', 'unsubscribed', 'likes', 'comments']
var list = []

const Stat = ({ token, group }) => {
	const [stat, setStat] = useState([])
	const [sortParam, setSortParam] = useState(localStorage.sortParam || 'likes')
	const [sortOrder, setSortOrder] = useState(localStorage.sortOrder || 'desc')
	const [paramSum, setParamSum] = useState({ likes: 0, subscribed: 0, comments: 0 })
	let paramSumUpd = { likes: 0, subscribed: 0, comments: 0 }

	function getStat(from = 0, to = Math.floor(+new Date() / 1000)) {
		setStat([])
		paramSumUpd = { likes: 0, subscribed: 0, comments: 0 }
		let i = 0
		let timerId = setTimeout(function tick() {
			let g = group[i]
			bridge.send('VKWebAppCallAPIMethod', {
				"method": "stats.get",
				"params": { "group_id": g.id, "timestamp_from": from, "timestamp_to": to, "interval": "all", "access_token": token, "v": "5.86" }
			})
				.then(result => {
					let stat = result.response[0].activity
					if (stat == undefined)
						stat = {}
					keys.forEach(e => { if (e in stat == false) stat[e] = 0 })
					stat.subscribed = stat.subscribed - stat.unsubscribed
					setStat(oldArray => [...oldArray, {
						id: g.id,
						name: g.name,
						img: g.photo_200,
						screen_name: g.screen_name,
						likes: stat.likes,
						subscribed: stat.subscribed,
						comments: stat.comments
					}])

					for(let key in paramSumUpd)
						paramSumUpd[key] += stat[key]
					setParamSum(paramSumUpd)

					i++
					if (i < group.length)
						timerId = setTimeout(tick, 100);
				})
		}, 100)
	}

	function socr(n) {
		var p = '';
		n = Number(n);
		if (n >= 1000000000) { n = n / 1000000000; p = " B"; }
		else if (n >= 1000000 && n < 1000000000) { n = n / 1000000; p = " M"; }
		else if (n >= 1000 && n < 1000000) { n = n / 1000; p = " K"; }
	
		if (p != '' && n % 1 != 0)
		  n = n.toFixed(1);
		else
		  n = Math.ceil(n);
	
		return n + ' ' + p;
	}

	function changeSortParam(value) {
		setSortParam(value)
		localStorage.sortParam = value
	}

	function changeSortOrder(value) {
		setSortOrder(value)
		localStorage.sortOrder = value
	}


	return (
		<div>
			<header>
				<ul id="menu">
					<li onClick={() => { changeSortParam('subscribed') }}>
						<i className="fas fa-user-alt"></i>
						<span>{socr(paramSum.subscribed)}</span>
					</li>
					<li onClick={() => { changeSortParam('likes') }}>
						<i className="fas fa-heart"></i>
						<span>{socr(paramSum.likes)}</span>
					</li>
					<li onClick={() => { changeSortParam('comments') }}>
						<i className="fas fa-comment"></i>
						<span>{socr(paramSum.comments)}</span>
					</li>
					<li>
						<span>Статистика за: </span>
						<SelectDay getStat={getStat} />
						<select onChange={(e) => { changeSortOrder(e.target.value) }}>
							<option value='desc'>По убыванию</option>
							<option value='asc'>По возрастанию</option>
						</select>
					</li>
				</ul>
			</header>
			<List stat={stat} sortOrder={sortOrder} sortParam={sortParam} />
		</div>
	)
}

export default Stat;

