import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import PropTypes from 'prop-types';

let color = ['75 170 85', '200 190 80', '200 190 80', '200 190 80', '80 200 139']
let getColor = (i, a = 0.75) => {return 'rgb(' + color[i % color.length] + ' / ' + a + ')'} 

function sort(arr, order = 'desc', param = 'likes'){
    return arr.sort((a, b) => {
        if(order == 'asc')
            b = [a, a = b][0]
        if (a[param] > b[param])
          return -1
        if (a[param] < b[param])
          return 1
        return 0
    })
}

function socr(n) {
    var p = '';
    n = Number(n);
    if (n >= 1000000000) { n = n / 1000000000; p = " B"; }
    else if (n >= 1000000 && n < 1000000000) { n = n / 1000000; p = " M"; }
    else if (n >= 1000 && n < 1000000) { n = n / 1000; p = " K"; }

    if (p != '' && n % 1 != 0)
      n = n.toFixed(1);
    else
      n = Math.ceil(n);

    return n + ' ' + p;
}

const List = ({ stat, sortOrder, sortParam }) => {
    stat = sort(stat, sortOrder, sortParam)
    console.log(stat, sortParam)
    return (
		stat.length == 0 ? <div id="board">Load...</div>:
		<div id="board">
				{stat.map((e, i) => (
                    <div className='item'>
                        <img src={e.img}></img>
                        <div className='shadow' style={{background: getColor(i), boxShadow: '0 0 20px ' + getColor(i, 0.1)}}></div>
                        <div className='info'>
                            <span className='name'>{e.name}</span>
                            <i className="fas fa-user-alt" aria-hidden="true">
                                <span>{socr(e[sortParam])}</span>
                            </i>
                        </div>
                    </div>
				))}
		</div>
	)
}

export default List;